import { CONTACT_EMAIL_SUPPORT } from "@rp/common/settings"
import { tagManagerEvents } from "@rp/react/web/common/vendors/utils"
import { SxProps, Typography } from "@rp/react/web/mui/core"

type Props = {
  onClick?: (e: React.MouseEvent<HTMLAnchorElement, MouseEvent>) => void
  sx?: SxProps
}

const ContactSupport = ({ onClick, sx }: Props) => {
  const onClickInner = (_e: React.MouseEvent<HTMLAnchorElement, MouseEvent>) => {
    tagManagerEvents.contactSupport()
    onClick?.(_e)
  }
  return (
    <Typography
      sx={{
        color: "primary.main",
        ...sx,
      }}
      component="span"
      onClick={onClickInner}
    >
      {CONTACT_EMAIL_SUPPORT}
    </Typography>
  )
}

export { ContactSupport }
